.simpay-styled .stripe-button-el {
  text-decoration: none; }

.simpay-checkout-form--loading::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  top: 0; }

.simpay-checkout-form--loading .simpay-form-control {
  opacity: 0.65; }

.simpay-errors {
  margin-bottom: 15px; }
  .simpay-styled .simpay-errors {
    font-family: Roboto, "Open Sans", Segoe UI, sans-serif;
    color: #eb1c26; }

.simpay-test-mode-badge-container {
  text-align: center; }
  .simpay-test-mode-badge-container .simpay-test-mode-badge {
    background-color: #f7e633;
    color: #333;
    border-radius: 5px;
    font-family: Roboto, "Open Sans", Segoe UI, sans-serif;
    font-weight: 700;
    font-size: 11px;
    text-transform: uppercase;
    padding: 5px 9px; }
